/* font-family: 'Pinyon Script', cursive;
font-family: 'Poppins', sans-serif; */

@import url("https://fonts.googleapis.com/css2?family=Pinyon+Script&family=Poppins:wght@100;400&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  /* Default font */
  font-size: 16px;

  /* Colors */
  --main-bg-color: #f1e3db;
  --secondary-bg-color: #e8c4c3;
  --assets-color: #e89eb9;
  --text-color: #000000;
}

.wrapper {
  max-width: 1500px;
  margin: 0 auto;
}

body {
  padding: 0;
  margin: 0;
  background-color: var(--main-bg-color);
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  color: var(--text-color);
  min-height: 100vh;
}

/* Scroll bar styling  */
body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  background: var(--main-bg-color);
}

body::-webkit-scrollbar-thumb {
  background: var(--assets-color);
}

header {
  height: 5rem;
  top: 0.9rem;
}

.logo {
  font-family: "Pinyon Script", cursive;
  font-size: 4rem;
  text-decoration: none;
  color: inherit;
}

.logo:hover {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

.menu-items ul {
  list-style: none;
}

.menu-items a {
  color: inherit;
  text-decoration: none;
}
.menu-items a:hover {
  color: inherit;
  cursor: pointer;
  transform: scale(1.1);
}

.navbar-brand-short {
  display: none;
}

.side-bar {
  position: fixed;
  background-color: var(--secondary-bg-color);
  width: 8rem;
}

.sidebar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.sidebar-item {
  padding-bottom: 2em;
  font-size: 1.2rem;
}

.sidebar-item:nth-last-child(3) {
  margin-top: auto;
}

.sidebar-link {
  text-decoration: none;
  color: inherit;
}

.sidebar-link:hover {
  text-decoration: none;
  color: inherit;
}

.sidebar-logo {
  font-family: "Pinyon Script", cursive;
  font-weight: bold;
  font-size: 3rem;
  margin-top: 0.9rem;
}

.title {
  width: 30rem;
  margin-bottom: 1rem;
  margin-top: 0;
}

.subtitle {
  width: 30rem;
  margin-bottom: 2rem;
}

.welcome-container {
  top: 20vh;
}

.btn-find-bouquet {
  color: inherit;
  background-color: var(--assets-color);
  padding: 1.2rem 6.25rem;
  border-radius: 5px;
  text-decoration: none;
}

.btn-find-bouquet:hover {
  color: inherit;
  background-color: var(--assets-color);
  padding: 1.2rem 6.25rem;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
}

.img-container {
  position: relative;
  display: inline-block;
  overflow: hidden;
  padding: 0;
  line-height: 0;
}

.flower-img {
  background-image: url(./img/wecome-flower.png);
  background-size: cover;
  height: 40rem;
  transform: rotate(-9.99deg);
}

.card-img {
  height: 23rem;
  object-fit: cover;
  cursor: pointer;
}

.card-img:hover {
  transform: scale(1.1);
}

.browse-bouquet {
  width: 100%;
  text-align: center;
  margin-bottom: 10rem;
}

.btn-view-more {
  color: inherit;
  background-color: var(--assets-color);
  padding: 1.2rem 7.25rem;
  border-radius: 5px;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  margin: 0 auto;
}

.btn-view-more:hover {
  text-decoration: none;
  color: inherit;
}

.btn-email-submit {
  background-color: var(--assets-color);
  border-radius: 5px;
  float: right;
  margin-bottom: 10rem;
}

footer {
  bottom: 0;
  left: 0;
  right: 0;
  margin-bottom: 0;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  /* Styles */
  .navbar-brand-short {
    display: inline-block;
  }

  .navbar-brand {
    display: none;
  }

  body {
    overflow-x: hidden;
    background-image: url(img/wecome-flower.png);
    background-repeat: no-repeat;
    background-position-x: 4rem;
    background-position-y: 10vh;
  }

  .wrapper {
    max-width: 370px;
  }

  .side-bar {
    display: none;
  }

  .img-container {
    display: none;
  }

  .welcome-container {
    margin-top: 15vh;
  }

  .welcome-container .title {
    margin-top: 10vh;
  }

  .browse-bouquet {
    margin-top: 3rem;
  }
}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width: 321px) {
  /* Styles */
  .wrapper {
    max-width: 900px;
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .img-container {
    display: none;
  }

  .welcome-container {
    height: 100vh;
  }

  .welcome-nav {
    height: 100vh;
  }

  .side-bar {
    display: none;
  }

  footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  footer ul {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  footer a {
    color: inherit;
    text-decoration: none;
    padding: 1rem;
  }
}

/* Smartphones (portrait) ----------- */
@media only screen and (max-width: 320px) {
  /* Styles */
  .navbar-brand-short {
    display: inline-block;
  }

  .navbar-brand {
    display: none;
  }

  .side-bar {
    display: none;
  }

  .welcome-nav {
    top: 21.3rem;
  }

  .welcome-container {
    height: 100vh;
  }
}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  /* Styles */
  .wrapper {
    max-width: 768px;
  }

  body {
    overflow-x: hidden;
    background-image: url(img/wecome-flower.png);
    background-repeat: no-repeat;
    background-position-x: 20rem;
    background-position-y: 10vh;
  }

  .welcome-container {
    width: 10rem;
  }

  .welcome-container .title {
    margin-top: 24vh;
    width: 24rem;
  }

  .browse-bouquet {
    margin-top: 3rem;
  }
}

/* iPads (portrait) ----------- */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) {
  /* Styles */

  .wrapper {
    max-width: 600px;
    margin: 0 auto;
  }

  .welcome-container {
    height: 100vh;
  }
}

/* Desktops and laptops ----------- */
@media only screen and (min-width: 1000px) {
  /* Styles */

  .wrapper {
    max-width: 1200px;
    margin-left: 10rem;
  }

  .side-bar {
    display: inline-block;
  }

  .img-container {
    display: inline-block;
  }

  .footer-item {
    display: none;
  }
}

/* Large screens ----------- */
@media only screen and (min-width: 1824px) {
  /* Styles */
  .wrapper {
    max-width: 1200px;
    margin: 0 auto;
  }

  .footer-item {
    display: none;
  }
}

/* iPhone 4 ----------- */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  /* Styles */
  .welcome-nav {
    max-width: 10rem;
  }

  .title {
    width: 17rem;
  }

  .subtitle {
    width: 21rem;
  }

  .welcome-container {
    height: 100vh;
  }
}
